import React from "react";
import "./ExperienceCard.css";
const ExperienceCard = ({ jobs }) => {
  function renderJobs() {
    return jobs.map((job) => (
      <div className="about-experience-card">
        <h3>{job.company}</h3>
        <span>
          {job.started_at} - {job.ended_at}
        </span>
        <h4>{job.role}</h4>
        <p>{job.main_activities}</p>
      </div>
    ));
  }
  return renderJobs();
};

export default ExperienceCard;
